import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "./player.scss";

class Player extends Component {
  constructor(props) {
    super(props);

    this.state = { isOpen: false };
  }

  toggleModal = () => {
    this.setState({
      isOpen: !this.state.isOpen
    });
  };

  render() {

    return (
      <div className={`${this.props.className} player`}>
        <span onClick={this.toggleModal} className="player__label">
          {this.props.playerName}
        </span>
        <div
          className={`player__modal ${
            this.state.isOpen ? "player__modal--open" : ""
          }`}
        >
          <div className="player__container container">
            <div className="player__details">
              <header className="player__header">
                <p className="player__name">{this.props.playerName}</p>
                <i onClick={this.toggleModal} className="player__close">
                  <FontAwesomeIcon icon={["far", "times"]} />
                </i>
              </header>
              <div className="player__stats">
                <div className="player__stat">
                  <p>Overall record</p>
                  <p>
                    {this.props.totalWon}-{this.props.totalDrawn}-
                    {this.props.totalLost}
                  </p>
                </div>
                <div className="player__stat">
                  <p>Trophies</p>
                  <p>
                    <i
                      className={`player__trophy player__trophy--${this.props.trophies}`}
                    >
                      <FontAwesomeIcon icon={["fas", "trophy"]} />
                    </i>
                    <i
                      className={`player__trophy player__trophy--${this.props.trophies}`}
                    >
                      <FontAwesomeIcon icon={["fas", "trophy"]} />
                    </i>
                    <i
                      className={`player__trophy player__trophy--${this.props.trophies}`}
                    >
                      <FontAwesomeIcon icon={["fas", "trophy"]} />
                    </i>
                    <i
                      className={`player__trophy player__trophy--${this.props.trophies}`}
                    >
                      <FontAwesomeIcon icon={["fas", "trophy"]} />
                    </i>
                    <i
                      className={`player__trophy player__trophy--${this.props.trophies}`}
                    >
                      <FontAwesomeIcon icon={["fas", "trophy"]} />
                    </i>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Player;
